/* Configurações e temas *
/
@import 'config';
// Reset e normalização
@import 'normalize';

// Mixins e variáveis
@import 'mixins';

// Estrutura base
@import 'home';
@import 'menu';
@import 'about';
@import 'projects';
@import 'contact';

// Responsividade
@import 'responsive';

// Temas (novo bloco)
@import 'themes/light';
@import 'themes/dark';
// @import 'themes/hydra'; // quando quiseres ativar o modo Hydra
*/
:root {
  --green: #00FF00;
  --white: #FFFFFF;
  --black: #000000;
}

body {
  background: var(--bg);
  color: var(--fontColor);
  font-family: helvetica;
}

ul {
  font-family: helvetica;
}

li {
  list-style: circle;
}

.list {
  list-style: square;
}

#msg {
  font-family: monospace;
}

.light-theme {
  --bg: var(--green);
  --fontColor: var(--black);
  --btnBg: var(--black);
  --btnFontColor: var(--white);
}

.dark-theme {
  --bg: var(--black);
  --fontColor: var(--green);
  --btnBg: var(--white);
  --btnFontColor: var(--black);
}

.btn {
  position: absolute;
  top: 20px;
  left: 250px;
  height: 50px;
  width: 50px;
  border-radius: 50%;
  border: none;
  color: var(--btnFontColor);
  background-color: var(--btnBg);
}